import React, { useRef, useState, useCallback } from "react";
import style from "./HomeContent.scss";
import classnames from "classnames/bind";
import SlickList from "../../SlickList";
import BookList from "../../BookList";
import VerticalScroll from "../../VerticalScroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDown, faAngleUp } from "@fortawesome/free-solid-svg-icons";
import { v4 } from "uuid";
import { useDispatch, useSelector } from "react-redux";
import { getAllBooks } from "../../../reducer/books";

const category = [
  "",
  "",
  "IT",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
]

const cx = classnames.bind(style);
const HomeContent = ({
  booklist,
  handleClick,
  sliderBooks,
  recentBooks,
  popularBooks,
  type,
  done,
}) => {
  //const [isMobile, setIsMobile] = useState(false);
  const [hide, setHide] = useState(true);
  const [page, setPage] = useState(1);
  const dispatch = useDispatch();
  const {
    allBookDone,
  } = useSelector((state) => state.books);

  const loadMoreRef = useRef(null);

  const handleMore = () => {
    if (!allBookDone) {
      dispatch(getAllBooks("", page + 1));
      setPage(page + 1);
    }
    if(loadMoreRef.current){
      loadMoreRef.current.blur();
    }
  };

  const toggleHide = () => {
    setHide((cur) => !cur);
  };

  return (
    <section className={cx("home-content")}>
      <SlickList books={sliderBooks} />
      <VerticalScroll
        title={"  "}
        books={recentBooks.slice(0, 15)}
      />
      <VerticalScroll title={"  "} books={popularBooks} />
      <section className={cx("books-wrapper")}>
        <section className={cx("book-category-section")}>
          <div
            className={cx(["book-category-div", `${hide ? `hide` : `more`}`])}
          >
            <ul className={cx("book-category-ul")}>
              {hide
                ? category.slice(0, 7).map((item) => (
                    <li
                      key={v4()}
                      className={cx(
                        `book-category-li`,
                        `${type === item ? `selected` : ""}`
                      )}
                      onClick={handleClick}
                      data-value={item}
                    >
                      {item}
                    </li>
                  ))
                : category.map((item) => (
                    <li
                      key={v4()}
                      className={cx(
                        `book-category-li`,
                        `${type === item ? `selected` : ""}`
                      )}
                      onClick={handleClick}
                      data-value={item}
                    >
                      {item}
                    </li>
                  ))}
            </ul>
          </div>
          <div className={cx("more-container")} onClick={toggleHide}>
            {hide ? " " : ""}
            <FontAwesomeIcon
              className={cx("more")}
              icon={hide ? faAngleDown : faAngleUp}
            />
          </div>
        </section>
        <BookList
          booklist={booklist}
          handleMore={handleMore}
          from={"home"}
          type={type}
          done={done}
          ref={loadMoreRef}
        />
      </section>
    </section>
  );
};

export default HomeContent;
